'use client';

import { createEpisode, deleteEpisode } from "@/services/episode.service";
import { useState } from "react";

export default function EpisodesManager({ content }: { content: any }) {
  const [seasonId, setSeasonId] = useState("");
  const [title, setTitle] = useState("");
  const [videoUrl, setVideoUrl] = useState("");
  const [thumbnail, setThumbnail] = useState("");
  const [duration, setDuration] = useState("");

  // state ŸÑŸÑÿ±ÿ≥ÿßŸÑÿ©
  const [successMessage, setSuccessMessage] = useState("");

  const handleCreate = async () => {
    if (!content?.uuid) {
      setSuccessMessage("Ÿäÿ±ÿ¨Ÿâ ÿßÿÆÿ™Ÿäÿßÿ± ÿßŸÑŸÖÿ≠ÿ™ŸàŸâ ÿ£ŸàŸÑÿßŸã");
      return;
    }

    try {
      await createEpisode({
        content_id: content.uuid, // ‚úÖ UUID
        season_id: seasonId ? Number(seasonId) : null,
        title,
        video_url: videoUrl,
        thumbnail: thumbnail || null,
        duration: duration || null,
      });

      // ÿπÿ±ÿ∂ ÿ±ÿ≥ÿßŸÑÿ© ÿßŸÑŸÜÿ¨ÿßÿ≠
      setSuccessMessage("ÿ™ŸÖ ÿ•ÿ∂ÿßŸÅÿ© ÿßŸÑÿ≠ŸÑŸÇÿ© ÿ®ŸÜÿ¨ÿßÿ≠");

      // ŸÖÿ≥ÿ≠ ÿßŸÑÿ±ÿ≥ÿßŸÑÿ© ÿ®ÿπÿØ ÿ´ÿßŸÜŸäÿ™ŸäŸÜ
      setTimeout(() => setSuccessMessage(""), 2000);

      // ÿ•ÿπÿßÿØÿ© ÿ∂ÿ®ÿ∑ ÿßŸÑÿ≠ŸÇŸàŸÑ
      setTitle("");
      setVideoUrl("");
      setThumbnail("");
      setDuration("");
      setSeasonId("");
    } catch (error: any) {
      console.error("ERROR:", error.response?.data || error.message);
      setSuccessMessage("ÿ≠ÿØÿ´ ÿÆÿ∑ÿ£ ÿ£ÿ´ŸÜÿßÿ° ÿ•ÿ∂ÿßŸÅÿ© ÿßŸÑÿ≠ŸÑŸÇÿ©");
      setTimeout(() => setSuccessMessage(""), 3000);
    }
  };

  return (
    <div className="space-y-6">
      <h3 className="text-xl font-bold">
        ÿßŸÑÿ≠ŸÑŸÇÿßÿ™ (
        {content.seasons?.reduce(
          (total: number, season: any) =>
            total + (season.episodes?.length || 0),
          0
        )}
        )
      </h3>

      {/* ÿ±ÿ≥ÿßŸÑÿ© ÿßŸÑŸÜÿ¨ÿßÿ≠ */}
      {successMessage && (
        <p className="bg-green-600 text-white p-2 rounded text-center">
          {successMessage}
        </p>
      )}

      {/* ÿ•ÿ∂ÿßŸÅÿ© ÿ≠ŸÑŸÇÿ© */}
      <div className="grid md:grid-cols-3 gap-3 bg-gray-800 p-4 rounded-xl">
        <select
          value={seasonId}
          onChange={(e) => setSeasonId(e.target.value)}
          className="p-2 bg-gray-700 rounded"
        >
          <option value="">ÿ®ÿØŸàŸÜ ŸÖŸàÿ≥ŸÖ (ŸÅŸäŸÑŸÖ)</option>
          {content.seasons?.map((s: any) => (
            <option key={s.id} value={s.id}>
              ÿßŸÑŸÖŸàÿ≥ŸÖ {s.season_number}
            </option>
          ))}
        </select>

        <input
          placeholder="ÿπŸÜŸàÿßŸÜ ÿßŸÑÿ≠ŸÑŸÇÿ©"
          value={title}
          onChange={(e) => setTitle(e.target.value)}
          className="p-2 bg-gray-700 rounded"
        />

        <input
          placeholder="ÿ±ÿßÿ®ÿ∑ ÿßŸÑŸÅŸäÿØŸäŸà"
          value={videoUrl}
          onChange={(e) => setVideoUrl(e.target.value)}
          className="p-2 bg-gray-700 rounded"
        />

        <input
          placeholder="ÿ±ÿßÿ®ÿ∑ ÿßŸÑÿµŸàÿ±ÿ© ÿßŸÑŸÖÿµÿ∫ÿ±ÿ© (ÿßÿÆÿ™Ÿäÿßÿ±Ÿä)"
          value={thumbnail}
          onChange={(e) => setThumbnail(e.target.value)}
          className="p-2 bg-gray-700 rounded"
        />

        <input
          placeholder="ÿßŸÑŸÖÿØÿ© (ŸÖÿ´ÿßŸÑ: 45:00)"
          value={duration}
          onChange={(e) => setDuration(e.target.value)}
          className="p-2 bg-gray-700 rounded"
        />

        <button
          onClick={handleCreate}
          className="bg-green-600 hover:bg-green-700 transition rounded p-2"
        >
          ÿ•ÿ∂ÿßŸÅÿ© ÿßŸÑÿ≠ŸÑŸÇÿ©
        </button>
      </div>

      {/* ÿπÿ±ÿ∂ ÿßŸÑÿ≠ŸÑŸÇÿßÿ™ */}
      <div className="space-y-3">
        {content.seasons?.map((season: any) =>
          season.episodes?.map((ep: any) => (
            <div
              key={ep.id}
              className="bg-gray-800 p-3 rounded-xl flex justify-between items-center"
            >
              <div>
                <p className="font-semibold">
                  ÿßŸÑŸÖŸàÿ≥ŸÖ {season.season_number} - {ep.title}
                </p>
                <p className="text-sm text-gray-400">
                  {ep.duration} | {ep.video_url}
                </p>
              </div>

              <button
                onClick={() => deleteEpisode(ep.id)}
                className="bg-red-600 px-3 py-1 rounded"
              >
                ÿ≠ÿ∞ŸÅ
              </button>
            </div>
          ))
        )}
      </div>
    </div>
  );
}
